/* Full-page container with universe + neural network background */
.home-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
  z-index: 1;
  color: #ffffff;
}

/* Optional overlay to darken the video for readability */
.home-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 30, 0.5); /* dark overlay */
  z-index: -1;
}

/* Home page title */
.home-title {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 40px;
  color: #ffffff;
  text-shadow: 0 0 15px rgba(0, 200, 255, 0.8); /* subtle neon glow */
}

/* Form buttons - glow effect for tech theme */
.add-user-btn,
.remove-user-btn,
.start-btn {
  border: none;
  cursor: pointer;
  border-radius: 10px;
  padding: 12px 24px;
  font-size: 1rem;
  margin-top: 20px;
  transition: all 0.3s ease;
  color: white;
  text-shadow: 0 0 5px #000;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

/* Add User button - green neon style */
.add-user-btn {
  background-color: #22c55e;
  box-shadow: 0 0 20px #22c55e;
}
.add-user-btn:hover {
  background-color: #16a34a;
  box-shadow: 0 0 30px #22c55e;
}

/* Remove User button - red neon style */
.remove-user-btn {
  background-color: #ef4444;
  box-shadow: 0 0 20px #ef4444;
}
.remove-user-btn:hover {
  background-color: #dc2626;
  box-shadow: 0 0 30px #ef4444;
}

/* Start Scraping button - blue neon style */
.start-btn {
  background-color: #2563eb;
  box-shadow: 0 0 20px #2563eb;
}
.start-btn:hover {
  background-color: #1d4ed8;
  box-shadow: 0 0 30px #2563eb;
}

/* Horizontal buttons container */
.buttons-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 30px;
}

/* User input forms styling */
.user-input-form {
  background-color: rgba(0, 0, 50, 0.6); /* translucent futuristic card */
  padding: 20px;
  border-radius: 15px;
  margin-bottom: 20px;
  width: 90%;
  max-width: 600px;
  box-shadow: 0 0 25px rgba(0, 200, 255, 0.5);
}

/* Input fields */
.user-input-form input {
  width: 100%;
  padding: 12px;
  margin-bottom: 12px;
  border-radius: 10px;
  border: none;
  background-color: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  font-size: 1rem;
}
.user-input-form input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}


.full-screen-loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.4); /* semi-transparent overlay */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  flex-direction: column;
  gap: 12px;
}

.full-screen-loader p {
  font-size: 1.2rem;
  font-weight: 600;
  color: #00c8ff;
  text-shadow: 0 0 8px rgba(0, 200, 255, 0.6);
  margin-top: 12px;
}



.csv-upload-btn {
  position: fixed;           
  top: 20px;                 
  right: 20px;               
  background-color: rgba(50, 130, 230, 0.8); 
  color: white;              
  padding: 12px 20px;        
  border-radius: 10px;       
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2); 
  backdrop-filter: blur(8px);       
  -webkit-backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  transition: all 0.3s ease;
  z-index: 10000;           
}

.csv-upload-btn:hover {
  background-color: rgba(30, 100, 210, 0.9);
  transform: translateY(-2px);
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.3);
}
