/* src/components/BackgroundVideo.css */
.video-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1; /* behind all content */
}

.video-content {
  width: 100%;
  height: 100%;
  object-fit: cover; /* cover the whole screen */
  filter: brightness(0.7); /* dim for better text readability */
}
