.results-container {
  position: relative;
  min-height: 100vh;
  padding: 40px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ffffff;
  z-index: 1;
}

.results-container::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('../assets/background.mp4') no-repeat center center / cover;
  /* if using video tag instead, place a video behind container */
  filter: brightness(0.5);
  z-index: -1;
}

.back-button {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 10px;
  color: #e0f7ff;
  font-size: 1rem;
  cursor: pointer;
  margin-bottom: 20px;
  padding: 8px 16px;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  transition: all 0.3s ease;
}

.back-button:hover {
  text-decoration: none;
  background-color: rgba(79, 70, 229, 0.2);
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.3);
}

.results-title {
  font-size: 2rem;
  font-weight: bold;
  color: #e0f7ff;
  text-shadow: 0 0 10px rgba(0, 200, 255, 0.5);
  margin-bottom: 30px;
}

.user-section {
  margin-bottom: 40px;
  width: 80%;
  max-width: 1200px;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
}

.user-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #e0f7ff;
  text-shadow: 0 0 6px rgba(0, 200, 255, 0.5);
  margin-bottom: 20px;
}

.profiles-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
}
