.profile-card {
  background-color: rgba(255, 255, 255, 0.1); /* transparent */
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border-radius: 12px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2); /* soft shadow */
  padding: 20px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.profile-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.3);
}

.profile-img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 12px;
  border: 2px solid rgba(255, 255, 255, 0.3); /* optional glow border */
}

.profile-name {
  font-size: 1.2rem;
  font-weight: bold;
  color: #e0f7ff;
  text-shadow: 0 0 8px rgba(0, 200, 255, 0.5);
}

.profile-headline {
  font-size: 1rem;
  color: #cce7ff;
  margin: 6px 0;
}

.profile-location,
.profile-company {
  font-size: 0.9rem;
  color: #a0d4ff;
}
